import React from 'react'
import {Link} from 'react-router-dom'



const DetailsDisplay = (props) =>{
   console.log(props)
   const renderTable = ({restdata}) => {
      if(restdata){
         return restdata.map((item)=>{
            return(
              
               <tr>
                  <td><img src={item.thumb} alt="" style={{height:100,width:100}}/></td>
                 
                  <td><Link to={`/rest/${item._id}`}>{item.name}</Link></td> 
                      
                  <td>{item.locality}</td>
                 
                  <td>{item.Cuisine[0].name},{item.Cuisine[1].name}</td>
                  
                  <td>{item.cost}</td>
                  <td>{item.contact_number}</td>
               </tr>
               
               
            )
         })
      }
   }
    return(
          
          <div className="card">
          <center><h3 style={{color:"black"}}>Restaurants List</h3></center>  
          <thead>
             <table className="table table-bordered table-hover">
            
             <tr>
                <th style={{textAlign:"left"}}>Image</th>
                
                <th>Name</th>
                
                <th>Locality</th>
               
                <th>Cuisine</th>
              
                <th>Cost</th>
                
                <th>Phone number</th>
             </tr>
             
             <tbody>
             {renderTable(props)}
             </tbody>
             
             </table>
             </thead>
         
          
          </div>
       
      
  
    )
    
}
export default DetailsDisplay