import React ,{Component} from 'react';






const locurl="http://localhost:9780/location"
const resturl="http://localhost:9780/restaruntsPage?city=";

class Search extends Component{
   constructor(props){
      super(props)
      this.state={
         location:'',
        city:'',
        restarunt:'',
        Restarunt:'',
        rest:''
      
       
        
      }
   }
   

   
   handleCity =(event) =>{
     
      this.setState({city:event.target.value})
      this.setState({restarunts:''})
      var cityid = parseInt(event.target.value)
      sessionStorage.setItem('cityId',event.target.value)
      fetch(`${resturl}${cityid}`,{method:'GET'})
      .then((res)=>res.json())
      .then((data)=>{
         this.setState({restarunts:data})
      })
   }
   
   renderCity =(data)=>{
      if(data){
         return data.map((item)=>{
            return(
              
                  <option value={item.city}>
                   {item.name}
               </option>
               
            )
         })
      }
   }
   
   renderRest =(data)=>{
      if(data){
         return data.map((item)=>{
            return(
              
                  <option value={item._id}>
                     {item.name} | {item.locality}
               </option>
               
            )
         })
      }
   }
 handleRestarunt =(event)=>{
    console.log(event.target.value)
    this.props.restid(Number(event.target.value))
 }


     render(){
        return(
          
         <div class="imageContainer">
         <div id="logo">
            <b>S!</b>                           
         </div>
         <div class="heading">
            find the best restarunts,cafes,bars
          
         </div>
         <div class="location">
         <center>
           <select onChange={this.handleCity}>
              {this.renderCity(this.state.location)}
              
           </select>
            
          {/*<input  list="restarunt" name="rest" id="rest"/>*/}
           <select id="restarunt" onChange={this.handleRestarunt}>
              {this.renderRest(this.state.restarunts)}
              
             
           </select>
          
           </center>
           </div>
          
               </div>
                    
               
                             
        )
      
     }
     componentDidMount(){
        fetch((locurl),{method:'GET'})  
        .then((res)=>res.json())
        .then((data)=>{
          console.log(data)
          this.setState({location:data})
          this.setState({_id:data})
        })
     }

       
}


export default Search;
