import React,{Component} from 'react';
import axios from 'axios';
import DetailsDisplay from './DetailsDisplay'


import './DeatilsDisplay.css'
const restdetail="http://localhost:9780/restaruntslist"

class Details extends Component{
  constructor(props){
    super(props)
     this.state={
       cityId:parseInt (sessionStorage.getItem('cityId'))?parseInt (sessionStorage.getItem('cityId')):1,
       restlist:[],
       cuisine:'',
       test:''
     }
  
  }
  costFilter = (event)  =>{
     let  mealid = parseInt(this.props.match.params.id)
     let cost = event.target.value.split(',');
     let lcost = Number (cost[0]);
     let hcost = Number (cost[1]);
     let url;
     if(event.target.value !==''){
       url=`${restdetail}/${this.state.cityId}/${mealid}`
     }else{
      url=`${restdetail}/${this.state.cityId}/${mealid}?hcost=${hcost}&lcost=${lcost}`
     }
     axios.get(url)
       .then((response)=> {this.setState({restlist:response.data})})
  }
  

  cuisineFilter = (event) =>{
    sessionStorage.setItem('mealid',this.props.match.params.id)
    let  mealid = parseInt(this.props.match.params.id)
    let cuisine = Number(event.target.value)
    axios.get(`${restdetail}/${this.state.cityId}/${mealid}?cuisine=${cuisine}`)
    .then((response)=>{this.setState({restlist:response.data})})
  }
  

  render(){
 
       return(
      
         <div>
           
              <div className="row" style={{boxShadow:"black"}}>
              
               <div class="col-md-1"style={{boxShadow:"black"}}>
                 <hr/>
                 <center>Cuisine</center>
               <div onChange={this.cuisineFilter}>
               
                 <div class="Cuisine">
                 <lable className="radio"style={{color:"red",}}>
                   <input type="radio" value="1" name="cuisine"/>North 
                 </lable>
                <lable className="radio"style={{color:"red",}}>
                   <input type="radio" value="2" name="cuisine"/>South
                </lable>
                <lable className="radio" style={{color:"red",}}>
 
                   <input type="radio" value="3" name="cuisine"/>Fast Food
                </lable>
                <lable className="radio"style={{color:"red",}}>
                   <input type="radio" value="4" name="cuisine"/>Chinese
                </lable>
               <label className="radio"style={{color:"red",}}>
               <input type="radio" value="5" name="cuisine"/>Street Food
               </label>
               </div>
              </div>
             
              <center>Cost</center>
         <hr/>
         <div onChange={this.costFilter}>
            <lable className="radio">
              <input type="radio" value="" name="range"/>All
            </lable>
           <lable className="radio">
              <input type="radio" value="0,500" name="range"/>0-500
           </lable>
           <lable className="radio">
              <input type="radio" value="501,1000" name="range"/>500-1000
            </lable>
            
          </div>
                
               </div>
               <div className="col-md-10">
                  <DetailsDisplay restdata={this.state.restlist}/>
                  
               </div>
             
             
              </div>    
             
         </div>
        
       )
  }
  componentDidMount(){
   
      sessionStorage.setItem('mealid',this.props.match.params.id)
      let  mealid = parseInt(this.props.match.params.id)
      console.log(`${restdetail}/${this.state.cityId}/${mealid}`)
      axios.get(`${restdetail}/${this.state.cityId}/${mealid}`)
      .then((response)=>{this.setState({restlist:response.data})})
  }
  
}



export default Details;