import React from 'react'

const Forms = () =>{
   return(
     <div className="container">
        <div className="panel panel-danger">
           <div className="panel-heading">
              Place Order
           </div>
           <div className="panel-body">
             <div className="form-group">
                <lable className="control-lable">Order_Id:</lable>
                <br/>
                <input type="text" name="Order_Id"
                className="form-control"></input>
             </div>
             
        
             <lable className="control-lable">Name:</lable>
             <br/>
             <input type="text" name="Name"
             className="form-control"></input>
            
             
             <lable className="control-lable">Mobile NO:</lable>
             <br/>
             <input type="text" name="Mobile No"
             className="form-control"></input>
            
           
             <lable className="control-lable">Mail:</lable>
             <br/>
             <input type="text" name="Mail"
             className="form-control"></input>
             
             
             <lable className="control-lable">How many Person:</lable>
             <br/>
             <select className="Person"
             >
                <option>1</option>
                <option>2</option>
                <option>3</option>
                <option>4</option>
                <option>5</option>
             </select>
               
               </div>
             </div>
             </div>
       
       
     
   )
  
}
export default Forms;