import React ,{Component}from 'react'
import StarRating from './StarRating'
import axios from 'axios'
import { Tab, Tabs, TabList, TabPanel } from 'react-tabs';
import {Link} from 'react-router-dom'
import 'react-tabs/style/react-tabs.css';








const restdetail="http://localhost:9780/restaruntsdetails"

class Restarunt extends Component{
   constructor(props){
      super(props)
       this.state={
          rest:''
       }

   }
   

   backbutton = (event) =>{
      let mealid =  sessionStorage.getItem('mealid')
      this.props.history.push(`/detail/${mealid}`)
   }
   render(){
      var {rest}= this.state
     
       
      
    return(
        
      
                 <div className="container">
                     <div className="panel panel-info" style={{color:"red"}}>
                  <h3><center>{rest.name}</center></h3> 
                  </div>       
                   <div>
                    <center><h3 style={{color:"red"}}>SELECT YOUR BEST YOUR RESTARARUNTS</h3></center> 
                   </div>
                                                                
                   <div className="maincontainer">
                      <div className="thecard">
                        <div className="front"><h4 style={{fontFamily:"Franklin Gothic Medium', 'Arial Narrow"}}>Touch Me</h4>
                          <img src={rest.thumb}alt="" style={{height:200,width:400}}/>
                           <h3 style={{color:"navy"}}>Name:{rest.name}</h3>
                          
                           <h3 style={{color:"navy"}}>Address:{rest.address}</h3>
                        </div>
                        <div className="back">Touch out
                        <StarRating/>
                     <h3>Phone Number:{rest.contact_number}</h3>
                        </div>
                      </div>                              
                   </div>       
                   <hr/>
                   <div>
    <Tabs>
    <TabList>
      <Tab><span style={{color:"red",fontSize:16}}>Overview</span></Tab>
      <Tab><span style={{color:"red",fontweight:"bolder",fontSize:16}}>Contact</span></Tab>
    </TabList>
 
    <TabPanel>
      <div className="about">About this place</div>
      <br/>
      <div className="cuisine">Cuisine</div>
      <br/>
      <div className="bakery">North Indian ,Fast Food</div>
      <br/>
      <div className="Cost">Average Cost</div>
      <br/>
      <div className="Average">$600 for two people (approx.)</div>                 
    </TabPanel>
    <TabPanel>
    
    <div className="Phone">Phone Number</div>
    <br/>
    <div className="Number">{rest.contact_number}</div>
    <br/>
    <div className="Name">{rest.name}</div>
    <br/>
    <div className="Address">{rest.address }</div>
    </TabPanel>
    
  </Tabs>
      </div>
        <br/>
         <button class="btn btn-danger btn-lg"
         onClick={this.backbutton}>Back</button> 
         &nbsp;    
         <Link to={`/Ordernow`} class="btn btn-warning btn-lg">Place Order</Link>                        
         </div> 

         
      )
      
   
   }
   componentDidMount(){
       let restid = parseInt(this.props.match.params.id)
      
       axios.get(`${restdetail}/${restid}`)
      .then((response)=> {this.setState({rest:response.data[0]})})
   }
}
export default Restarunt