import React  from 'react';
import Search from './Search'
import QuickSearch from './QuickSearch';


 const Home = (props) =>{
     
      const handleRestarunt =(data)=>{
      console.log(">>>data>>>",data)
      props.history.push(`/rest/${data}`)
   }
     return(
        <div>
           <Search restid={(data)=>{handleRestarunt(data)}}/>
           <QuickSearch/>
        </div>
     )
 }
 export default Home;
