import React from 'react'
import {Link} from 'react-router-dom'

const DisplaySearch = (props)=>{
   const listMeal = ({mealData}) =>{
      if(mealData){
         return mealData.map((item)=>{
            return(
               <Link to ={`/detail/${item.mealtype}`}>
                   
        
        <div className="tileContainer">
           <div className="tileComponent1">
                  
              <img src={item.thumb}alt="" style={{height:150,width:160}}/>
           </div>
           <div class="tileComponent">
              <div class="componentHeading">
                 {item.name}
              </div>
              <div class="componentSubHeading">
                Start your day with delicious enjoyable moment
            </div>
           </div>
           
        </div>  
              
                  
               </Link>
            )

            
         })
      }

   }
   return(
      <div>
        
        <div class="quicksearchcontainer">
        <p class="quicksearcheHeading">
           Quick searches
        </p>
        <p class="quicksearcheSubHeading">
           Discover restarunts by type of meal
           
        </p> 
        </div>
        
         {listMeal(props)}
      </div>
   )
}
export default DisplaySearch